import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: () => import('@/pages/index/template.vue')
    },
    {
      path: '/login',
      component: () => import('@/pages/login/template.vue')
    },
    {
      path: '/notebooks',
      component: () => import('@/pages/notebooks/template.vue')
    },
    {
      path: '/note',
      component: () => import('@/pages/note/template.vue')
    },
    {
      path: '/trash',
      component: () => import('@/pages/trash/template.vue')
    }
  ]
})



