import auth from "@/api/auth"
import router from '@/router'


const state = {
    user: null
}

const getters = {
    username: state => state.user === null ? '未登录' : state.user.username,
    
    slug: state => state.user === null ? '未' : state.user.username.charAt(0)
}

const mutations = {
    setUser(state,paylod) {
        state.user = paylod.user
    }
}

const actions = {
    login({ commit },{ username,password }) {
        return auth.login({ username,password })
        .then(res => {
            commit('setUser',{ user: res.data })
        })
    },

    register({ commit },{ username,password }) {
        return auth.register({ username,password })
        .then(res => {
            console.log(res.data)
            commit('setUser',{ user: res.data })
        })
    },

    checkLogin({ commit,state },paylod) {
        if(state.user !== null) return Promise.resolve()
        return auth.getInfo().then(res => {
              if(!res.isLogin) {
                router.push(paylod)
              }else {
                  commit('setUser',{ user: res.data})
              }
          }) 
    },

    logout({ commit },payload) {
        return auth.logout()
        .then(() => {
            commit('setUser',{ user: null })
            router.push(payload)
        })
    }

}

export default {
    state,
    getters,
    mutations,
    actions
}


