import Notebooks from './modules/notebooks'
import Notes from './modules/notes'
import Trash from './modules/trash'
import User from './modules/user'
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
    modules: {
        Notebooks,
        Notes,
        Trash,
        User
    }
})


